/**
 * Translation Service
 *
 * This service provides AI-powered translations for the Resource Library.
 * Uses Google Cloud Translation API for high-quality translations.
 */

// API Configuration - Replace with your actual API key in production
const API_KEY = process.env.REACT_APP_GOOGLE_TRANSLATE_API_KEY || 'YOUR_API_KEY';
const API_ENDPOINT = 'https://translation.googleapis.com/language/translate/v2';

// Translation cache to avoid redundant API calls
const translationCache = {};

// Language metadata - Now with additional languages
export const LANGUAGES = {
  en: { name: 'English', nativeName: 'English', dir: 'ltr', flag: '🇺🇸' },
  ar: { name: 'Arabic', nativeName: 'العربية', dir: 'rtl', flag: '🇦🇪' },
  tr: { name: 'Turkish', nativeName: 'Türkçe', dir: 'ltr', flag: '🇹🇷' },
  fr: { name: 'French', nativeName: 'Français', dir: 'ltr', flag: '🇫🇷' },
  ru: { name: 'Russian', nativeName: 'Русский', dir: 'ltr', flag: '🇷🇺' },
  zh: { name: 'Chinese', nativeName: '中文', dir: 'ltr', flag: '🇨🇳' },
  // Added languages
  ja: { name: 'Japanese', nativeName: '日本語', dir: 'ltr', flag: '🇯🇵' },
  ko: { name: 'Korean', nativeName: '한국어', dir: 'ltr', flag: '🇰🇷' },
  es: { name: 'Spanish', nativeName: 'Español', dir: 'ltr', flag: '🇪🇸' },
  de: { name: 'German', nativeName: 'Deutsch', dir: 'ltr', flag: '🇩🇪' },
  it: { name: 'Italian', nativeName: 'Italiano', dir: 'ltr', flag: '🇮🇹' },
  pt: { name: 'Portuguese', nativeName: 'Português', dir: 'ltr', flag: '🇵🇹' },
  nl: { name: 'Dutch', nativeName: 'Nederlands', dir: 'ltr', flag: '🇳🇱' },
  hi: { name: 'Hindi', nativeName: 'हिन्दी', dir: 'ltr', flag: '🇮🇳' },
  // Add more languages as needed
};

/**
 * Load the translation cache from localStorage
 */
const loadCache = () => {
  try {
    const cache = localStorage.getItem('translationCache');
    if (cache) {
      Object.assign(translationCache, JSON.parse(cache));
      console.log(`Loaded ${Object.keys(translationCache).length} cached translations`);
    }
  } catch (error) {
    console.warn('Failed to load translation cache:', error);
  }
};

/**
 * Save the translation cache to localStorage
 */
const saveCache = () => {
  try {
    localStorage.setItem('translationCache', JSON.stringify(translationCache));
  } catch (error) {
    console.warn('Failed to save translation cache:', error);

    // If storage is full, clear half the cache and try again
    if (error.name === 'QuotaExceededError') {
      const keys = Object.keys(translationCache);
      const halfLength = Math.floor(keys.length / 2);

      keys.slice(0, halfLength).forEach(key => delete translationCache[key]);

      try {
        localStorage.setItem('translationCache', JSON.stringify(translationCache));
      } catch (innerError) {
        console.error('Still failed to save cache after cleanup:', innerError);
      }
    }
  }
};

/**
 * Detect the language of a text using Google Cloud Translation API
 * @param {string} text - The text to detect the language of
 * @returns {Promise<string>} - The detected language code
 */
export const detectLanguage = async (text) => {
  if (!text || text.trim().length < 3) return 'en';

  // If we're in development mode and don't have an API key, use simulated detection
  if (process.env.NODE_ENV === 'development' && API_KEY === 'YOUR_API_KEY') {
    return simulateLanguageDetection(text);
  }

  try {
    const response = await fetch(`${API_ENDPOINT}/detect?key=${API_KEY}`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        q: text
      })
    });

    if (!response.ok) {
      throw new Error(`Language detection API returned ${response.status}`);
    }

    const data = await response.json();

    if (data.data &&
        data.data.detections &&
        data.data.detections.length > 0 &&
        data.data.detections[0].length > 0) {
      const detectedLanguage = data.data.detections[0][0].language;

      // Check if we support this language
      return LANGUAGES[detectedLanguage] ? detectedLanguage : 'en';
    }

    return 'en';
  } catch (error) {
    console.error('Language detection failed:', error);
    // Fallback to simulation if API fails
    return simulateLanguageDetection(text);
  }
};

/**
 * Simulated language detection (used when API key is not available)
 * @param {string} text - Text to detect
 * @returns {string} - Detected language code
 */
function simulateLanguageDetection(text) {
  // Simple detection using character ranges
  if (/[\u0600-\u06FF]/.test(text)) return 'ar'; // Arabic
  if (/[\u0400-\u04FF]/.test(text)) return 'ru'; // Cyrillic/Russian
  if (/[\u4E00-\u9FFF]/.test(text)) return 'zh'; // Chinese
  if (/[\u3040-\u30FF]/.test(text)) return 'ja'; // Japanese
  if (/[\uAC00-\uD7AF]/.test(text)) return 'ko'; // Korean
  if (/[çğıöşü]/i.test(text)) return 'tr'; // Turkish
  if (/[éèêëàâçùûüÿœæ]/i.test(text)) return 'fr'; // French
  if (/[áéíóúüñ]/i.test(text)) return 'es'; // Spanish
  if (/[äöüß]/i.test(text)) return 'de'; // German
  if (/[àèéìíòóù]/i.test(text)) return 'it'; // Italian

  return 'en'; // Default to English
}

/**
 * Translate text using Google Cloud Translation API
 * @param {string} text - The text to translate
 * @param {string} targetLang - The target language code
 * @param {string} sourceLang - The source language code (default: auto-detect)
 * @param {Object} options - Additional options like glossary
 * @returns {Promise<string>} - The translated text
 */
export const translateText = async (text, targetLang, sourceLang = 'auto', options = {}) => {
  // Don't translate empty strings or when target is the same as source
  if (!text || !text.trim()) {
    return text;
  }

  // Don't translate if target and source are the same
  if (sourceLang !== 'auto' && targetLang === sourceLang) {
    return text;
  }

  console.log(`TranslationService: Translating from ${sourceLang} to ${targetLang}:`,
    text.length > 50 ? text.substring(0, 50) + '...' : text);

  // Build cache key - include glossary ID if provided
  const glossaryId = options.glossary ? options.glossary : 'default';
  const cacheKey = `${sourceLang}:${targetLang}:${glossaryId}:${text}`;

  // Check cache first
  if (translationCache[cacheKey]) {
    console.log('TranslationService: Using cached translation');
    return translationCache[cacheKey];
  }

  try {
    // If we're in development mode and don't have an API key, use simulated translation
    if (process.env.NODE_ENV === 'development' && API_KEY === 'YOUR_API_KEY') {
      const result = simulateTranslation(text, targetLang, sourceLang, options);
      console.log('TranslationService: Simulated translation result:', result);
      return result;
    }

    // Prepare request body
    const requestBody = {
      q: text,
      target: targetLang,
      format: 'text'
    };

    // Add source language if specified
    if (sourceLang !== 'auto') {
      requestBody.source = sourceLang;
    }

    // Add glossary if provided in options
    if (options.glossary) {
      requestBody.glossaryConfig = {
        glossary: options.glossary
      };
    }

    // Call Google Translation API
    const response = await fetch(`${API_ENDPOINT}?key=${API_KEY}`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(requestBody)
    });

    if (!response.ok) {
      throw new Error(`Translation API returned ${response.status}`);
    }

    const data = await response.json();

    if (data.data &&
        data.data.translations &&
        data.data.translations.length > 0) {

      const translatedText = data.data.translations[0].translatedText;

      // Cache the result
      translationCache[cacheKey] = translatedText;

      // Periodically save cache (not every time to improve performance)
      if (Object.keys(translationCache).length % 10 === 0) {
        saveCache();
      }

      return translatedText;
    }

    throw new Error('Translation API returned an unexpected response format');

  } catch (error) {
    console.error('Translation failed:', error);

    // Fallback to simulation if API fails
    if (process.env.NODE_ENV === 'development') {
      console.log('TranslationService: Falling back to simulation');
      return simulateTranslation(text, targetLang, sourceLang, options);
    }

    return text; // Return original text on failure
  }
};

/**
 * Simulated translation (used when API key is not available)
 * @param {string} text - Text to translate
 * @param {string} targetLang - Target language
 * @param {string} sourceLang - Source language
 * @param {Object} options - Additional options
 * @returns {string} - Translated text
 */
function simulateTranslation(text, targetLang, sourceLang, options = {}) {
  // Mock translations for a few common phrases
  const mockTranslations = {
    // English to Arabic
    'auto:ar:default:Resource Library': 'مكتبة الموارد',
    'auto:ar:default:Search': 'بحث',
    'auto:ar:default:Dashboard': 'لوحة التحكم',
    'auto:ar:default:Analytics': 'التحليلات',
    'auto:ar:default:Settings': 'الإعدادات',

    // English to French
    'auto:fr:default:Resource Library': 'Bibliothèque de ressources',
    'auto:fr:default:Search': 'Rechercher',
    'auto:fr:default:Dashboard': 'Tableau de bord',
    'auto:fr:default:Analytics': 'Analyses',
    'auto:fr:default:Settings': 'Paramètres',

    // English to Spanish
    'auto:es:default:Resource Library': 'Biblioteca de recursos',
    'auto:es:default:Search': 'Buscar',
    'auto:es:default:Dashboard': 'Panel de control',
    'auto:es:default:Analytics': 'Análisis',
    'auto:es:default:Settings': 'Configuración',

    // English to Japanese
    'auto:ja:default:Resource Library': 'リソースライブラリ',
    'auto:ja:default:Search': '検索',
    'auto:ja:default:Dashboard': 'ダッシュボード',
    'auto:ja:default:Analytics': '分析',
    'auto:ja:default:Settings': '設定',

    // English to Korean
    'auto:ko:default:Resource Library': '리소스 라이브러리',
    'auto:ko:default:Search': '검색',
    'auto:ko:default:Dashboard': '대시보드',
    'auto:ko:default:Analytics': '분석',
    'auto:ko:default:Settings': '설정',

    // Add more sample translations as needed
  };

  // Check if we have a predefined translation with glossary
  const glossaryId = options.glossary ? options.glossary : 'default';
  const mockKey = `${sourceLang}:${targetLang}:${glossaryId}:${text}`;

  if (mockTranslations[mockKey]) {
    return mockTranslations[mockKey];
  }

  // If we don't have a mock translation, format the result like "[language] original"
  return `[${targetLang}] ${text}`;
}

/**
 * Translate an entire object's string properties
 * @param {Object} obj - The object to translate
 * @param {string} targetLang - The target language code
 * @param {Object} options - Translation options (e.g., glossary)
 * @returns {Promise<Object>} - The translated object
 */
export const translateObject = async (obj, targetLang, options = {}) => {
  if (!obj || typeof obj !== 'object' || targetLang === 'en') {
    return obj;
  }

  const result = Array.isArray(obj) ? [] : {};

  for (const key in obj) {
    if (Object.prototype.hasOwnProperty.call(obj, key)) {
      const value = obj[key];

      if (typeof value === 'string') {
        // Translate strings
        result[key] = await translateText(value, targetLang, 'auto', options);
      } else if (value !== null && typeof value === 'object') {
        // Recursively translate nested objects
        result[key] = await translateObject(value, targetLang, options);
      } else {
        // Keep non-string values as is
        result[key] = value;
      }
    }
  }

  return result;
};

/**
 * Process a batch of translations to reduce API calls
 * @param {string[]} texts - Array of texts to translate
 * @param {string} targetLang - Target language code
 * @param {string} sourceLang - Source language code
 * @param {Object} options - Translation options
 * @returns {Promise<string[]>} - Array of translated texts
 */
export const translateBatch = async (texts, targetLang, sourceLang = 'auto', options = {}) => {
  if (!texts || !texts.length || targetLang === 'en') {
    return texts;
  }

  // Filter out texts that are already in cache
  const glossaryId = options.glossary ? options.glossary : 'default';
  const uncachedTexts = [];
  const uncachedIndices = [];
  const results = new Array(texts.length);

  // Check cache first for each text
  texts.forEach((text, index) => {
    const cacheKey = `${sourceLang}:${targetLang}:${glossaryId}:${text}`;
    if (translationCache[cacheKey]) {
      results[index] = translationCache[cacheKey];
    } else {
      uncachedTexts.push(text);
      uncachedIndices.push(index);
    }
  });

  // If all texts were in cache, return immediately
  if (uncachedTexts.length === 0) {
    return results;
  }

  try {
    // If we're in development mode and don't have an API key, use simulated translations
    if (process.env.NODE_ENV === 'development' && API_KEY === 'YOUR_API_KEY') {
      const simulatedTranslations = await Promise.all(
        uncachedTexts.map(text => simulateTranslation(text, targetLang, sourceLang, options))
      );

      // Merge simulated translations back into results array
      uncachedIndices.forEach((originalIndex, i) => {
        results[originalIndex] = simulatedTranslations[i];

        // Cache the translations
        const cacheKey = `${sourceLang}:${targetLang}:${glossaryId}:${uncachedTexts[i]}`;
        translationCache[cacheKey] = simulatedTranslations[i];
      });

      return results;
    }

    // Prepare request body for batch translation
    const requestBody = {
      q: uncachedTexts,
      target: targetLang,
      format: 'text'
    };

    // Add source language if specified
    if (sourceLang !== 'auto') {
      requestBody.source = sourceLang;
    }

    // Add glossary if provided in options
    if (options.glossary) {
      requestBody.glossaryConfig = {
        glossary: options.glossary
      };
    }

    // Call Google Translation API for batch translation
    const response = await fetch(`${API_ENDPOINT}?key=${API_KEY}`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(requestBody)
    });

    if (!response.ok) {
      throw new Error(`Translation API returned ${response.status}`);
    }

    const data = await response.json();

    if (data.data &&
        data.data.translations &&
        data.data.translations.length === uncachedTexts.length) {

      // Process and cache the translations
      data.data.translations.forEach((translation, i) => {
        const originalIndex = uncachedIndices[i];
        const originalText = uncachedTexts[i];
        const translatedText = translation.translatedText;

        // Store in results array
        results[originalIndex] = translatedText;

        // Cache the translation
        const cacheKey = `${sourceLang}:${targetLang}:${glossaryId}:${originalText}`;
        translationCache[cacheKey] = translatedText;
      });

      // Save cache periodically
      saveCache();

      return results;
    }

    throw new Error('Batch translation API returned an unexpected response format');

  } catch (error) {
    console.error('Batch translation failed:', error);

    // Fallback to individual translations
    const fallbackTranslations = await Promise.all(
      uncachedTexts.map(text => translateText(text, targetLang, sourceLang, options))
    );

    // Merge fallback translations back into results array
    uncachedIndices.forEach((originalIndex, i) => {
      results[originalIndex] = fallbackTranslations[i];
    });

    return results;
  }
};

/**
 * Get list of supported languages from API
 * @returns {Promise<Array>} - List of supported languages
 */
export const getSupportedLanguages = async () => {
  try {
    // If we're in development mode without API key, return predefined languages
    if (process.env.NODE_ENV === 'development' && API_KEY === 'YOUR_API_KEY') {
      return Object.keys(LANGUAGES).map(code => ({
        language: code,
        name: LANGUAGES[code].name
      }));
    }

    const response = await fetch(`${API_ENDPOINT}/languages?key=${API_KEY}&target=en`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      }
    });

    if (!response.ok) {
      throw new Error(`Language list API returned ${response.status}`);
    }

    const data = await response.json();

    if (data.data && data.data.languages) {
      return data.data.languages;
    }

    throw new Error('Language list API returned an unexpected response format');
  } catch (error) {
    console.error('Failed to get supported languages:', error);
    // Return predefined languages as fallback
    return Object.keys(LANGUAGES).map(code => ({
      language: code,
      name: LANGUAGES[code].name
    }));
  }
};

// Initialize by loading cache
loadCache();

export default {
  LANGUAGES,
  translateText,
  translateObject,
  translateBatch,
  detectLanguage,
  getSupportedLanguages
};
